/* Reset and Common Styles */
* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  font-family: "Jost", sans-serif;
  font-weight: 400;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-heading {
    font-size: 5rem;
    color: #333;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
    margin: 15rem;
    border-bottom: .3rem solid #333;
    text-shadow: .2rem .2rem .5rem #555;
  }

.container {
 
/* ====== Section 1 ====== */
.section-1 {
  width: 100%;
  height: 100vh;
  background: url("../images/bg.jpg"), linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.3)) center no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .section-1-heading {
    font-size: 6rem;
    font-weight: 700;
    color: #fff;
    background-color: #e41c6f;
    padding: 0 3rem;
    margin-bottom: 5rem;
    text-transform: uppercase;
    letter-spacing: 1rem;
    border-bottom: 3rem solid #fff;
    text-shadow: .2rem .2rem 1rem #555;
    transform: skew(-10deg);
  }

  .person-img {
    width: 25rem;
    height: 25rem;
    object-fit: cover;
    border-radius: 50%;
    border: .7rem dotted #e41c6f;
    background-color: rgba(255,255,255,0.7);
    padding: .5rem;
    opacity: 0.9;
  }

  .person-name {
    font-size: 2rem;
    color: #fff;
    background-color: #e41c6f;
    margin: 5rem 0;
    padding: .5rem 3rem;
    letter-spacing: 0.3rem;
    transform: skew(-10deg);
  }

  .section-1-btn {
    font-size: 1.7rem;
    font-weight: 700;
    color: #333;
    background-color: #fff;
    border: .3rem solid #333;
    padding: .5rem 1rem;
    text-transform: uppercase;
    letter-spacing: .3rem;
  }
 } /*  Section 1 */

  /*  Navbar */
  .navbar {
    width: 100%;
    height: 4rem;
    background-color: #e41c6f;
    box-shadow: .2rem .2rem .5rem#555;
    position: absolute;
    z-index: 300;
  }

  .sticky {
    position: fixed;
    top: 0;
  }

  .change.navbar-link {
    color:#fff;
  }

  .navbar-link {
    font-size: 1.7rem;
    font-weight: 600;
    color: #333;
    margin: 0 3rem;
    letter-spacing: .3rem;
    text-transform: uppercase;
    position: relative;
    transition: color 0.2s;

    &::hover {
      color: #fff;

    }

    &::before {
      content: "";
      width: 0;
      height: .2rem;
      background-color: #fff;
      position: absolute;
      right: -15%;
      bottom: -0.3rem;
      transition: width 0.2s;
    }

    &:hover::before {
      width: 130%;
    }
  }

   
/* ====== Section 2 ====== */
.section-2 {
  width: 100%;
  height: 100%;
  padding: 15rem 0 10rem 0;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
 
  .progress-bars-wrapper {
    margin-bottom: 10rem;

    .progress-bar {
      width: 60rem;
      height: 3rem;
      background-color: #ccc;
      margin: 1.5rem 0;
      border-radius: .3rem;
      box-shadow: .2rem .2rem 1rem #555;
      position: relative;

      .progress-text {
        position: absolute;
        left: 1rem;
        font-size: 1.5rem;
        color: #fff;

        span {
          margin: 0 .5rem;
          font-size: 1.8rem;
          font-weight: 700;
        }
      }

      .progress-percent {
        // width: 90%;
        width: 0;
        height: 100%;
        background-color: #e41c6f;
        border-radius: 0.3rem;
        transition: width .5s .5s ease-in-out;
      }
    } /*  progress-bar */
  } /*  progress-bars-wrapper */

  .services {
    width: 100%;
    height: 30vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    .service {
      text-align: center;

      &:first-child, 
      &:last-child {
        align-self: flex-start;
      }
      
      .svg-inline--fa {
        font-size: 10rem;
        color: #888;
        margin-bottom: 2rem;
        text-shadow: .1rem .1rem .5rem #555;
      }

      h2 {
        font-size: 2rem;
        color: #fff;
        background-color: #777;
        width: 25rem;
        letter-spacing: .3rem;
        transform: skew(-10deg);
        box-shadow: .2rem .2rem .5rem #555;
      }
    }
 
  } /*  Services */
 
  
} /*  Section 2 end */
 
 
/* ====== Section 3 ====== */
  .section-3 {
    width: 100%;
    height: 100%;
    padding: 10rem 0;
    background-color: #eef;
    display: flex;
    flex-direction: column;
    align-items: center;

    .projects-wrapper {
      width: 80%;
      flex-wrap: wrap;

      .project {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 1rem;
        background-color: #fff;
        box-shadow: .3rem .3rem .5rem #777;
        position: relative;
        overflow: hidden;
        transition: box-shadow 0.5s;

        &:hover {
          box-shadow: 1rem 1rem 1rem #777;
        }

        &:hover .project-text {
          top: 5rem;
          transition: top .3s .3s;
        } 

        &:hover .project-link {
          bottom: 5rem;
          transition: bottom .3s .3s;
        }

        &:hover .project-img {
          opacity: .2;
        }

        .project-text {
          text-align: center;
          letter-spacing: .1rem;
          position: absolute;
          top: -10rem;
          z-index: 10;
          transition: top 0.3s;

          .project-name {
            font-size: 3rem;
            font-weight: 700;
            color: #333;
            text-transform: capitalize;
          }

          .project-technologies {
            font-size: 1.8rem;
            color: #e41c6f;
          }
        }

        .project-img {
          width: 40rem;
          opacity: opacity .3s;
        }

        .project-link {
          font-size: 2rem;
          font-weight: 500;
          color: #333;
          letter-spacing: 0.1rem;
          border: .3rem solid #e41c6f;
          padding: 0 1rem;
          background-color: #fff;
          position: absolute;
          bottom: -5rem;
          transition: bottom .3s;
        }
      } /*  project */


    } /*  project wrapper */

  } /*  Section 3 end */

   
/* ====== Section 4 ====== */
  .section-4 {
    width: 100%;
    height: 70vh;
    background-color: #333;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    .section-4-heading {
      color: #fff;
      box-shadow: none;
      border-color:#fff;
      margin-bottom: 5rem;
    }

    .contact-form {
      width: 50rem;
      flex-direction: column;

      input, textarea {
        width: 100%;
        padding: .5rem;
        margin: .5rem 0;
        background-color: #eef;
        font-size: 1.5rem;
        border: .2rem solid #e41c6f;
        color: #555;
      }

      textarea {
        height: 10rem;
        max-height: 15rem;
        max-width: 100%;
      }

      .contact-form-btn {
        background-color: #e41c6f;
        color: #fff;
        text-transform: uppercase;
        letter-spacing: 1rem;
        cursor: pointer;
      }
    }
  } /*  section-4 */
 
 
/* ====== Section 5 ====== */
  .section-5 {
    width: 100%;
    height: 30vh;
    background-color: #222;
    flex-direction: column;

    .social-media {
      margin-bottom: 5rem;

      .social-media-link {
        margin: 0 2rem;
        padding: .7rem;
        background-color: #e41c6f;
        border-radius: 0.5rem;
        display: inline-block;

        .svg-inline--fa {
          font-size: 3rem;
          color: #fff;
        }
        
      }
    }

    .copyright {
      color: #aaa;
      font-size: 2rem;
      letter-spacing: .2rem;
      font-weight: 300;
      text-align: center;
    }


  } /*  section-5 */
 
} /*  Container */

  
 
 
/* ====== Responsive ====== */
@media(max-width: 1200px){
  .services {
    height: auto;
    flex-wrap: wrap;

    .service {
      margin: 3rem 2rem;
    }
  }

  .section-4 {
    height: 80vh;
  }

  footer {
    margin-top: 5rem;
  }
   
} /*  1200px */

@media(max-width: 800px){
  .section-1-heading {
    font-size: 4rem;
  }
  .progress-bar {
    width: 50rem;
  }
} /*  800px */
 
@media(max-width: 600px){
  .section-1-heading {
    font-size: 3rem;
  }

  .navbar-link {
    margin: 0 2rem;
    font-size: 1.5rem;
  }

  .section-2 {
    padding: 10rem;
  }

  .section-2-heading {
    margin-bottom: 10rem;
  }

  .progress-bar {
    width: 45rem;
  }

  .project-img {
    width: 100%;
  }

  .contact-form {
    width: 40rem;
  }

  .copyright {
    width: 70%;
  }
} /*  600px */

@media (max-width: 450px) {
  html {
    font-size: 45%;
  }

  .section-1-heading {
    font-size: 2.5rem;
  }

  .navbar-link {
    margin: 0 1.5rem;
  }

  .section-2-heading {
    text-align: center;
  }

  .progress-bar {
    width: 40rem;
  }

  footer {
    margin-top: 10rem;
  }
} /*  450px */